import React, { useState, useEffect } from 'react';
import EntityContainer from 'modules/entity/containers';
import { Scrollbars } from 'react-custom-scrollbars-2';
import { LoadMoreVisible } from 'shared/components';
import { ErrorBoundary, Loader } from 'shared/components';
import ChatMember from './ChatMember';
import List from '@material-ui/core/List';
import Fab from '@material-ui/core/Fab';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import TextField from '@material-ui/core/TextField';
import { useDebounce } from 'use-debounce';
import MembersFilter from './MembersFilter';
import IconButton from '@material-ui/core/IconButton';
import TuneIcon from '@material-ui/icons/Tune';
import { useEntityForm } from '../../../../modules/entity/hooks/useEntityForm';
import { useAppState } from '../../../../shared/state';
import { useChatContext } from '../../../../shared/contexts';
import { WhiteModal } from '../../../../shared/components/Elements';
import { useNotistack } from '../../../../shared/hooks';
import { loadOneRequest } from '../../../../modules/entity/actions';
import { useDispatch } from 'react-redux';

const AddChatMembers = (props: any) => {
	const [page, setPage] = useState(1);
	const { onSuccess, activeChat, staffMembersLength } = props;

	const { companyId } = useAppState();
	const { entityId } = useChatContext();

	const [selectedMembers, setSelectedMembers] = useState<any[]>([]);
	const [selectedMembersFull, setSelectedMembersFull] = useState<any[]>([]);
	const [loading, setLoading] = useState(false);
	const [value, setValue] = useState('all');
	const [options, setOptions] = useState({
		is_individual: 'true',
		is_business: 'true',
		is_member: 'true'
	});

	const dispatch = useDispatch();

	const [selected, setSelected] = useState('All');
	const { showNotification } = useNotistack();

	const { handleRequest: handleAddMember } = useEntityForm({
		entity: 'chatmembers',
		name: `Allchatmembers-${entityId}-${staffMembersLength}`,
		method: 'post',
		url: `/${companyId}/ims/groups/${entityId}/members/`,
		values: selectedMembersFull.reduce(
			(acc, curr) => [
				...acc,
				{ user: { id: curr.id }, role: 'MEMBER', company: { id: curr.companyId } }
			],
			[]
		),
		cb: {
			success: (data) => {
				//We must refetch ims object because of members count which is changed.
				dispatch(
					loadOneRequest({
						id: entityId,
						entity: 'ims-chats',
						name: `GroupChat-${entityId}`,
						url: `/${companyId}/ims/groups/${entityId}/`,
						params: {},
						primaryKey: 'id',
						relations: ''
					})
				);

				showNotification({ message: 'Members successfully added', variant: 'success' });
			},
			error: (error) => {
				showNotification({ message: 'Something went wrong', variant: 'error' });
			},
			finally: () => {
				setLoading(false);
				onSuccess();
			}
		}
	});

	useEffect(() => {
		(() => {
			switch (value) {
				case 'all':
					setSelected('All');
					break;
				case 'individual':
					setSelected('Individual Clients');
					break;
				case 'clients':
					setSelected('Business Client Users');
					break;
				case 'staff':
					setSelected('Staff');
					break;
				default:
					return null;
			}
		})();
	}, [value]);

	const handleChange = (event: any) => {
		const value = event.target.value;
		setValue(value);
		setLoading(true);
		setPage(1);
		setOpen(false);
		switch (value) {
			case 'all':
				setOptions({
					is_individual: 'true',
					is_business: 'true',
					is_member: 'true'
				});
				break;
			case 'individual':
				setOptions({
					is_individual: 'true',
					is_business: 'false',
					is_member: 'false'
				});
				break;
			case 'clients':
				setOptions({
					is_individual: 'false',
					is_business: 'true',
					is_member: 'false'
				});
				break;
			case 'staff':
				setOptions({
					is_individual: 'false',
					is_business: 'false',
					is_member: 'true'
				});
				break;
			default:
				return null;
		}
	};

	const { is_individual, is_business, is_member } = options;
	const [query, setQuery] = useState('');
	const [open, setOpen] = useState(false);
	const [searchQuery] = useDebounce(query, 300);

	useEffect(() => {
		setPage(1);
	}, [searchQuery]);

	const onClickHandler = (id: any, member: any) => {
		if (selectedMembers.includes(id)) {
			const filteredIds = selectedMembers.filter((selId) => selId !== id);
			const filteredMembers = selectedMembersFull.filter((m) => m.id !== id);
			setSelectedMembers(filteredIds);
			setSelectedMembersFull(filteredMembers);
		} else {
			setSelectedMembers((prev) => [...prev, id]);
			setSelectedMembersFull((prev) => [
				...prev,
				{ id: member.id, companyId: member?.company?.id }
			]);
		}
	};

	const addMembersToChat = () => {
		setLoading(true);
		handleAddMember();
	};

	return (
		<div className="members-list relative">
			<EntityContainer.All
				entity="clientMembers"
				name={`AllClientMembers-${entityId}-${staffMembersLength}`}
				url={`/${companyId}/my/company/client_users/`}
				primaryKey={(value) => `${value.id}-${value.company.id}`}
				appendData
				isUniq={false}
				params={{
					page,
					limit: 20,
					extra: {
						search: searchQuery,
						is_individual,
						is_business,
						is_member,
						exclude_ids: (activeChat.members as any)
							.filter((member: any) => member.role.toUpperCase() !== 'KICKED')
							.reduce((acc: any[], curr: any) => [...acc, curr?.user.id], [])
							.join(',')
					}
				}}
				// eslint-disable-next-line no-unused-vars
				onInit={(data) => {
					// console.log("data", data);
					setLoading(false);
				}}
			>
				{({ items, isFetched, meta }) => {
					return (
						<>
							<div className="members-list__settings">
								<div className="members-list__search">
									<TextField
										label="Search members"
										value={query}
										onChange={(e) => {
											setPage(1);
											setQuery(e.target.value);
										}}
										variant="outlined"
										size="small"
										fullWidth
									/>
								</div>
								<div className="members-list__filter-btn pl-2.5">
									<IconButton aria-label="filter" color="default" onClick={() => setOpen(true)}>
										<TuneIcon />
									</IconButton>
								</div>
							</div>
							<div className="ml-2.5 flex items-center">
								<div className="mr-2.5">
									<strong>Filtering type:</strong>
								</div>
								<div className="mr-2.5">{selected}</div>
							</div>

							<Scrollbars className="rct-scroll" autoHide style={{ height: '300px' }}>
								<div className="members-list__list">
									{loading && <Loader tiled />}
									<>
										<List className="add-chat-member__list" dense>
											{(items as any).map((member: any, index: number) => (
												<ChatMember
													key={index}
													member={member}
													onClickHandler={onClickHandler}
													selectedMembers={selectedMembersFull}
													isAddingMember
												/>
											))}
										</List>
										{isFetched &&
											meta?.current_page &&
											meta?.last_page &&
											meta?.current_page < meta?.last_page && (
												<LoadMoreVisible setPage={() => setPage((prev) => prev + 1)} />
											)}
										{!isFetched ? (
											<div className="case-chat-loader flex items-center justify-center">
												<Loader />
											</div>
										) : null}
									</>
								</div>
							</Scrollbars>
						</>
					);
				}}
			</EntityContainer.All>
			{/*<Dialog*/}
			{/*	classes={{*/}
			{/*		paper: 'case-form-modal-paper visible-modal-paper',*/}
			{/*		container: 'case-form-paper-container visible-modal-paper-container'*/}
			{/*	}}*/}
			{/*	open={open}*/}
			{/*	onClose={() => setOpen(false)}*/}
			{/*	aria-labelledby="alert-dialog-title"*/}
			{/*	aria-describedby="alert-dialog-description"*/}
			{/*	maxWidth="xl"></Dialog>*/}
			{/*Group Chat Members and Add Members popup*/}
			<WhiteModal handleModalOpen={() => setOpen(false)} open={open}>
				<MembersFilter value={value} handleChange={handleChange} />
			</WhiteModal>

			<div className="absolute bottom-2.5 right-2.5">
				<Fab
					color="primary"
					aria-label="add"
					onClick={addMembersToChat}
					disabled={selectedMembers.length === 0}
				>
					<CheckCircleIcon />
				</Fab>
			</div>
		</div>
	);
};

export default function AddChatMembersWithErrorBoundary(props: any) {
	return (
		<ErrorBoundary>
			<AddChatMembers {...props} />
		</ErrorBoundary>
	);
}
