import React from 'react';
import { SwitchComp } from '../../../shared/components/Elements/Fields';

type Props = {
	text: string;
	Component: any;
	iconProp: any;
	field_name: string;
	handleChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
	property: any;
};

export function PermissionItem({
	text,
	Component,
	iconProp,
	field_name,
	handleChange,
	property
}: Props) {
	return (
		<div className="permissions__item mb-4 ">
			<div className="permission flex items-center rounded-xl bg-white p-3 shadow-lg dark:bg-kdark-dark333">
				<div className="icon flex-centered h-10 w-10 rounded-lg bg-primary">
					<div className="text-white">{<Component {...iconProp} />}</div>
				</div>
				<div className="ml-4 text-sm">{text}</div>
				<div className={'ml-auto'}>
					<SwitchComp
						handleChange={(e) => handleChange(e)}
						disabled={false}
						name={field_name}
						checked={property[field_name]}
					/>
				</div>
			</div>
		</div>
	);
}
