import React, { useCallback, useEffect, useRef, useState } from 'react';
import { useQueryParams, useSearchQuery } from '../../../shared/hooks';
import { useAppState } from '../../../shared/state';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { useGroupMessageSearch } from '../hooks';
import { Scrollbars } from 'react-custom-scrollbars-2';
import List from '@material-ui/core/List';
import EntityContainer from 'modules/entity/containers';
import { Loader, LoadMoreVisible } from '../../../shared/components';
import SearchMessageItem from '../chat-components/SearchMessageItem';
import { IMessage } from '../../../shared/interfaces';
import { AiOutlineSearch } from 'react-icons/ai';

export function GroupMessageSearch() {
	const [page, setPage] = useState(1);
	const { groupSearchOpen, companyId, setGroupMessageSearchQuery, setIsLastPageLoadedTriggered } =
		useAppState();
	const { query, setQuery, searchQuery } = useSearchQuery();
	const { toggleSearchSidebar } = useGroupMessageSearch();
	const { query: searchQueryParams } = useQueryParams();
	const { changeUrlParams } = useQueryParams<{ chat: number; searchedMessageId?: number }>();
	const inputRef = useRef(null);

	const listItemClickHandler = useCallback(
		(messageId) => {
			if (searchQuery.length > 0) {
				changeUrlParams({ chat: Number(searchQueryParams.chat), searchedMessageId: messageId });

				setIsLastPageLoadedTriggered(true);
			}
		},
		[searchQuery, searchQueryParams]
	);

	useEffect(() => {
		if (groupSearchOpen && inputRef.current) {
			setTimeout(() => {
				(inputRef?.current as unknown as HTMLInputElement).focus();
			}, 300);
		}
	}, [groupSearchOpen]);

	useEffect(() => {
		setGroupMessageSearchQuery(searchQuery);
	}, [searchQuery]);

	const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
		setPage(1);
		setQuery(e.target.value);
	};

	const closeSearchSidebar = () => {
		changeUrlParams({ chat: Number(searchQueryParams.chat) }, true);
		toggleSearchSidebar();
	};

	const chatId = searchQueryParams?.chat;

	return (
		<div className="flex h-full flex-col border-l p-1 pr-2 dark:bg-kdark-dark333">
			<div className="flex items-center justify-between">
				<IconButton aria-label="close" onClick={closeSearchSidebar} className="!mr-1">
					<CloseIcon />
				</IconButton>
				<div className="search-input relative flex-grow">
					<input
						type="search"
						className="group-search-input w-full rounded border p-2 pr-4 transition focus:outline-none focus:ring focus:ring-primary dark:bg-kdark-lighter dark:text-white"
						value={query}
						onChange={handleInputChange}
						ref={inputRef}
						placeholder="Type message keywords"
					/>
					<div className="hover:text-kprimary absolute right-3 top-3 cursor-pointer">
						{!query.length ? <AiOutlineSearch /> : null}
					</div>
				</div>
			</div>

			<div className="content flex-grow">
				<>
					<Scrollbars
						className="rct-scroll"
						autoHide
						style={{ height: 'calc(100vh - 104px)' }}
						onScroll={() => {}}
					>
						<List className="mb-0 p-0">
							{searchQuery.length && companyId ? (
								<EntityContainer.All
									entity="search-messages"
									name={`${companyId}-AllSearchMessages-${chatId}`}
									url={`/${companyId}/ims/message_search/`}
									appendData
									params={{
										page,
										limit: 20,
										extra: { search: searchQuery, group_chat_id: chatId }
									}}
									primaryKey="message_id"
								>
									{({ isFetched, items, meta }) => {
										if (!items.length && searchQuery && isFetched) {
											return (
												<p className="p-4 !text-xl font-bold dark:text-white">No messages found</p>
											);
										}

										return (
											<>
												{(items as IMessage[]).map((message) => {
													return (
														<SearchMessageItem
															key={message.message_id}
															searchQuery={searchQuery}
															listItemClickHandler={listItemClickHandler}
															message={message}
														/>
													);
												})}
												{!isFetched && (
													<div style={{ textAlign: 'center' }}>
														<Loader />
													</div>
												)}
												{isFetched &&
												meta?.current_page &&
												meta?.last_page &&
												meta?.current_page < meta?.last_page ? (
													<LoadMoreVisible setPage={() => setPage((prev) => prev + 1)} />
												) : null}
											</>
										);
									}}
								</EntityContainer.All>
							) : (
								<p className="!p-4 dark:text-white">Please search for messages</p>
							)}
						</List>
					</Scrollbars>
				</>
			</div>
		</div>
	);
}
