import React, { useState } from 'react';
import { useAxios } from 'shared/hooks';
import { useAppState } from 'shared/state';
import Actions from 'store/actions';
import { useDispatch } from 'react-redux';
const UpdateEntities = Actions.entities.updateEntitiesSuccess;

type Pin = null | string | number;

export function useChatMenu(pin_time: Pin, chatId: number, isFromChat = false) {
	const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
	const [managerRequested, setManagerRequested] = useState<boolean>(false);
	const { companyId, toggleChatPanel } = useAppState();
	const open = Boolean(anchorEl);
	const dispatch = useDispatch();

	const handleClick = (event: React.MouseEvent<HTMLElement>) => {
		setAnchorEl(event.currentTarget);
	};

	//pin and unpin handler
	const { fetchData: pinCase } = useAxios({
		url: `/${companyId}/ims/groups/${chatId}/pin/`,
		method: `${pin_time ? 'delete' : 'put'}`,
		cb: {
			success: (data, args) => {
				const [isPinning] = args;
				dispatch(
					UpdateEntities({
						entity: 'ims-chats',
						entityId: String(chatId),
						data: {
							pin_time: isPinning ? Date.now() : null
						}
					})
				);
				if (isFromChat) {
					dispatch(
						UpdateEntities({
							entity: 'ims-chats',
							entityId: String(chatId),
							data: {
								pin_time: isPinning ? Date.now() : null
							}
						})
					);
				}
			},
			error: (error) => {}
		}
	});

	const handleNotificationClose = (event?: React.SyntheticEvent, reason?: string) => {
		if (reason === 'clickaway') {
			return;
		}

		setManagerRequested(false);
	};

	const handleMenuItemClick = (menuType: string) => {
		switch (menuType) {
			case 'pin': {
				pinCase(true);
				break;
			}
			case 'unpin': {
				pinCase(false);
				break;
			}
			default: {
				return null;
			}
		}
	};

	const handleCloseMenu = () => {
		setAnchorEl(null);
	};

	return {
		open,
		handleClick,
		anchorEl,
		managerRequested,
		handleNotificationClose,
		handleMenuItemClick,
		handleCloseMenu
	};
}
