import { useEffect } from 'react';
import { parseUnreadMessages, useAxios } from 'shared/hooks';
import { useAppState } from 'shared/state';
import { useGroupUnreadLoad } from './useGroupUnreadLoad';
import { useChatContext } from '../../../shared/contexts';

export function useGroupInitialSettings() {
	const { setPageType, companyId } = useAppState();

	const { fetchGroupsUnreadMessagesCount } = useGroupUnreadLoad();

	useEffect(() => {
		if (companyId) fetchGroupsUnreadMessagesCount();
	}, [companyId]);

	useEffect(() => {
		setPageType(true);
		return () => {
			setPageType(false);
		};
	}, [setPageType]);
}
