import React from 'react';
import Switch from '@material-ui/core/Switch';

type Props = {
	className: string;
	handler: any;
	text: string;
	Component: any;
	iconProp: any;
	hasSwitch: boolean;
	field_name: string;
	handleChange: any;
	property: any;
	isMuteSetting?: any;
	isMute?: boolean;
};

function ChatSettingItem({
	className,
	handler,
	text,
	Component,
	iconProp,
	hasSwitch,
	field_name,
	handleChange,
	property,
	isMuteSetting,
	isMute
}: Props) {
	const clickHandler = () => () =>
		isMuteSetting ? handleChange({ target: { checked: !isMute } }) : handler(true);
	return (
		<div
			className={`chat-sett-toggler flex items-center dark:!bg-kdark-dark333 ${className}`}
			onClick={clickHandler()}
		>
			<div className="chat-sett-toggler__icon flex items-center justify-center text-white">
				{<Component {...iconProp} />}
			</div>
			<div className="chat-sett-toggler__name">{text}</div>
			{hasSwitch && !isMuteSetting && (
				<div className="chat-sett-toggler__switch">
					<Switch
						color="primary"
						edge="end"
						name={field_name}
						checked={property[field_name]}
						onChange={handleChange}
						inputProps={{ 'aria-labelledby': 'switch-list-label-group-admin' }}
					/>
				</div>
			)}
		</div>
	);
}

export default ChatSettingItem;
