import React, { useState, useEffect } from 'react';
import ContentLoader from 'react-content-loader';
import { ChatItem } from './ChatItem';
import EntityContainer from 'modules/entity/containers';
import { useAppState } from 'shared/state';
import { ErrorBoundary, LoadMoreVisible } from 'shared/components';
import { GroupMember, IGroupChat } from 'shared/interfaces';
import { useAuth } from 'modules/auth/hooks/useAuth';
import { useQueryParams } from 'shared/hooks';
import { sortChatList } from 'shared/services';
import { buildGroupParams, CHATS_MESSAGES_LIMIT } from 'shared/consts/groupChat.const';

type Props = {
	isUnreadFilter: boolean;
};

function GroupListInner({ isUnreadFilter }: Props) {
	const [page, setPage] = useState(1);
	const [searchPage, setSearchPage] = useState(1);
	const { companyId, groupSearchQuery, groupsUnreadCountData } = useAppState();
	const { userData } = useAuth();
	const { query } = useQueryParams();
	const isActiveChat = query.chat;

	useEffect(() => {
		setSearchPage(1);
		setPage(1);
	}, [groupSearchQuery]);

	return (
		<div className="group-sidebar__chats flex-grow overflow-y-auto pt-2">
			{companyId ? (
				<EntityContainer.All
					entity={'ims-chats'}
					name={`AllImsChats-${companyId}-${groupSearchQuery}`}
					url={`/${companyId}/ims/groups/`}
					params={{
						extra: buildGroupParams(groupSearchQuery, isUnreadFilter),
						limit: CHATS_MESSAGES_LIMIT,
						page: groupSearchQuery.length ? searchPage : page
					}}
					// primaryKey={groupSearchQuery.length ? 'message_id' : 'id'}
					appendData
					// replaceIds
					isCancellable={!!groupSearchQuery.length}
					cancelField="groupChat"
				>
					{({ items, isFetched, meta }) => {
						//3192's fix: remove unmatched groups on real-time when unread filter is active
						const mappedAndFilteredChatItems = (items as IGroupChat[]).filter((item) =>
							isUnreadFilter
								? groupsUnreadCountData[item.id] > 0 || item.id.toString() === isActiveChat
								: item
						);

						const sortedChatItems = sortChatList<IGroupChat>(mappedAndFilteredChatItems);

						return (
							<>
								{sortedChatItems.map((item) => {
									return (
										<React.Fragment key={item.id}>
											<ChatItem
												chatId={item.id}
												title={item.title}
												key={item.id}
												pin_time={item.pin_time}
												last_message_type={item.last_message_type}
												last_message_author={item.last_message_author}
												last_message_text={item.last_message_text}
												last_message_time={item.last_message_time}
												image={item.image?.thumbnail_150 || null}
												isMuted={
													item?.members?.find(
														(member: GroupMember) => member.user.id === userData.id
													)?.is_mute
												}
												draftMessage={item.draft_message}
												lastReadMessage={item.last_read_message}
											/>
										</React.Fragment>
									);
								})}
								<>
									{!isFetched ? (
										<>
											{[...Array(10)].map((_: any, key) => (
												<div className={'mb4'} key={key}>
													<ChatListLoader />
												</div>
											))}
										</>
									) : null}
								</>
								<>
									{isFetched &&
									meta?.current_page &&
									meta?.last_page &&
									meta?.current_page < meta?.last_page ? (
										<LoadMoreVisible
											setPage={() => {
												if (meta.current_page !== page) {
													setPage(meta.current_page as number);
												} else {
													groupSearchQuery.length
														? setSearchPage(searchPage + 1)
														: setPage(page + 1);
												}
											}}
										/>
									) : null}
								</>
							</>
						);
					}}
				</EntityContainer.All>
			) : null}
		</div>
	);
}

export function GroupList(props: Props) {
	return (
		<ErrorBoundary>
			<GroupListInner {...props} />
		</ErrorBoundary>
	);
}

function ChatListLoader() {
	return (
		<ContentLoader
			speed={2}
			width={362}
			height={72}
			viewBox="0 0 362 72"
			backgroundColor="#f3f3f3"
			foregroundColor="#ecebeb"
		>
			<circle cx="48" cy="33" r="24" />
			<rect x="79" y="14" rx="7" ry="7" width="217" height="18" />
			<rect x="80" y="42" rx="5" ry="5" width="155" height="11" />
			<rect x="351" y="12" rx="4" ry="4" width="12" height="32" />
			<rect x="313" y="13" rx="5" ry="5" width="30" height="11" />
		</ContentLoader>
	);
}
