import React from 'react';
import { GroupImage } from 'shared/interfaces';
import { useGroupRequest } from '../hooks/useGroupRequest';
import { ConfirmModal, ErrorBoundary } from 'shared/components';
import { GoMute, GoUnmute } from 'react-icons/go';
import { IoExitOutline } from 'react-icons/io5';
import { PermissionItem } from './PermissionItem';
import { ChatPermissionMenuItem } from './ChatPermissionMenuItem';
import { MdOutlineDelete } from 'react-icons/md';
import { FiEdit } from 'react-icons/fi';
import { useWhiteModal } from '../../../shared/hooks';
import { EditGroupForm } from './EditGroupForm';
import { useAppState } from '../../../shared/state';

type Props = {
	title: string;
	image: null | GroupImage;
	membersCount: number;
	isMuted: boolean | undefined;
	chatId: number;
	// handleMute: (chatId: number, changingData: { [key: string]: any }) => void;
	memberId: number | undefined;
	isOwner: boolean;
};

function PermissionsInner({
	title,
	image,
	membersCount,
	isMuted,
	chatId,
	memberId,
	isOwner
}: Props) {
	const {
		leaveGroupChat,
		permissions,
		handlePropertyChange,
		property,
		handlerSwitchMute,
		canLeaveChat,
		canDeleteChat,
		deleteVisible,
		deleteLoading,
		toggleDeleteVisible,
		deleteGroupChat,
		exitLoading,
		open,
		setExitOpen,
		imsData,
		currentUserProperty
	} = useGroupRequest({
		chatId,
		isMuted,
		memberId
	});

	const { isAdmin } = useAppState();

	const { open: openEditModal, modalHandler, WhiteModal } = useWhiteModal();
	return (
		<div className="w-72 ">
			<div className="permissions font-poppins">
				<div className="permissions__top">
					<div className="permission__header flex rounded-lg bg-primary p-2 text-white  shadow-lg">
						<div className="permissions__img flex-centered mr-2 h-14 w-14 flex-shrink-0 overflow-hidden rounded-md bg-white text-primary-dark">
							{image ? <img src={image.thumbnail_150} alt={title} /> : title?.[0].toUpperCase()}
						</div>
						<div className="permissions__header-text flex-grow ">
							<div className="flex-center-between text-base">
								<span>{title}</span>
								{isAdmin || isOwner || currentUserProperty?.is_allowed_edit_info ? (
									<div
										className="cursor-pointer transition hover:text-primary-dark"
										onClick={modalHandler}
									>
										<FiEdit />
									</div>
								) : null}

								<WhiteModal handleModalOpen={modalHandler} open={openEditModal}>
									<EditGroupForm
										onSuccess={modalHandler}
										title={imsData.title}
										description={imsData.description}
										image={imsData.image}
										id={imsData.id}
									/>
								</WhiteModal>
							</div>
							<div className="text-sm">{`${membersCount} members`}</div>
						</div>
					</div>
				</div>
				<div className="permissions__content mt-4">
					{/*Permission with Switch component*/}
					{permissions.map((permission) => {
						return (
							<React.Fragment key={permission.text}>
								{permission.show ? (
									<PermissionItem
										key={permission.text}
										text={permission.text}
										Component={permission.Component}
										iconProp={permission.iconProp}
										field_name={permission.field_name}
										handleChange={handlePropertyChange}
										property={property}
									/>
								) : null}
							</React.Fragment>
						);
					})}

					{/*Mute Notifications*/}
					<ChatPermissionMenuItem
						text={isMuted ? 'Unmute notifications' : 'Mute notifications'}
						handler={() => handlerSwitchMute(isMuted as boolean)}
						Icon={isMuted ? GoMute : GoUnmute}
					/>

					{/*Delete Chat Notifications*/}
					{canDeleteChat ? (
						<ChatPermissionMenuItem
							text="Delete Group"
							handler={toggleDeleteVisible}
							Icon={MdOutlineDelete}
						/>
					) : null}

					{/*Exit Group Chat*/}
					{canLeaveChat ? (
						<>
							<ChatPermissionMenuItem
								text="Leave group"
								handler={() => setExitOpen(true)}
								Icon={IoExitOutline}
							/>
						</>
					) : null}
					<ConfirmModal
						featureHandler={leaveGroupChat}
						isOpen={Boolean(open)}
						toggleHandler={() => setExitOpen((prev) => !prev)}
						title={`Are you sure to exit ${title} group`}
						hasLoader
						isLoading={exitLoading}
					/>
					<ConfirmModal
						featureHandler={deleteGroupChat}
						isOpen={Boolean(deleteVisible)}
						toggleHandler={toggleDeleteVisible}
						title={`Are you sure to delete  ${title} group`}
						hasLoader
						isLoading={deleteLoading}
					/>
				</div>
			</div>
		</div>
	);
}

export function Permissions(props: Props) {
	return (
		<ErrorBoundary isCompact>
			<PermissionsInner {...props} />
		</ErrorBoundary>
	);
}
