import React, { useState } from 'react';
import EntityContainer from 'modules/entity/containers';
import { Scrollbars } from 'react-custom-scrollbars-2';
import { ConfirmModal, LoadMoreVisible } from 'shared/components';
import { ErrorBoundary, Loader } from 'shared/components';
import ChatMember from './ChatMember';
import List from '@material-ui/core/List';
import TextField from '@material-ui/core/TextField';
import { useDebounce } from 'use-debounce';
import MemberChatSettings from './MemberChatSettings';
import { useDispatch } from 'react-redux';
import { useAppState } from '../../../../shared/state';
import Actions from 'store/actions';
import { useChatContext } from '../../../../shared/contexts';
import { WhiteModal } from '../../../../shared/components/Elements';
import { useNotistack } from '../../../../shared/hooks';
import { loadOneRequest } from '../../../../modules/entity/actions';
const { formRequest } = Actions.entity;

const MembersList = (props: any) => {
	const [page, setPage] = useState(1);
	const [query, setQuery] = useState('');
	const [searchQuery] = useDebounce(query, 300);
	const [open, setOpen] = useState(false);
	const [chatEditSettingModal, showChatEditSettingModal] = useState(false);
	const [loading, setLoading] = useState(false);
	const { companyId } = useAppState();
	const { entityId } = useChatContext();
	const dispatch = useDispatch();
	const { showNotification } = useNotistack();

	const {
		isAdminManager,
		isOwner,
		currentMember,
		staffMembersLength
		// chatId,
	} = props;

	const removeMember = (member: any) => {
		setLoading(true);
		dispatch(
			formRequest({
				entity: 'chatmembers',
				name: `Allchatmembers-${entityId}-${staffMembersLength}`,
				method: 'delete',
				url: `/${companyId}/ims/groups/${entityId}/members/${member.id}/`,
				deleteData: true,
				id: member.id,
				cb: {
					// eslint-disable-next-line @typescript-eslint/no-empty-function
					success: () => {
						//We must refetch ims object because of members count which is changed.
						dispatch(
							loadOneRequest({
								id: entityId,
								entity: 'ims-chats',
								name: `GroupChat-${entityId}`,
								url: `/${companyId}/ims/groups/${entityId}/`,
								params: {},
								primaryKey: 'id',
								relations: ''
							})
						);
						showNotification({ message: 'Successfully deleted', variant: 'success' });
					},
					// eslint-disable-next-line @typescript-eslint/no-empty-function
					error: (err) => {
						// NotificationManager.error(err.code);
						showNotification({ message: 'Something went wrong', variant: 'error' });
					},
					// eslint-disable-next-line @typescript-eslint/no-empty-function
					finally: () => {
						setOpen(false);
						setLoading(false);
					}
				},
				params: {},
				primaryKey: 'id',
				values: {}
			})
		);
	};

	const closeRemoveMemberHandler = () => setOpen(false);

	return (
		<div className="members-list">
			<EntityContainer.All
				entity="chatmembers"
				name={`Allchatmembers-${entityId}-${staffMembersLength}`}
				url={`/${companyId}/ims/${entityId}/members_list/`}
				appendData
				params={{
					page,
					limit: 20,
					extra: {
						search: searchQuery
					}
				}}
				// eslint-disable-next-line no-unused-vars
				onInit={(data) => {
					// console.log("data", data);
				}}
			>
				{({ items, isFetched, meta }) => {
					return (
						<>
							<div className="members-list__search p-4">
								<TextField
									// type="search"
									label="Search members"
									value={query}
									onChange={(e) => setQuery(e.target.value)}
									variant="outlined"
									size="small"
									fullWidth
								/>
							</div>
							<Scrollbars
								className="rct-scroll"
								autoHide
								/*style={{ height: getChatBoxHeight() }}*/ style={{ height: '300px' }}
							>
								<div className="members-list__list">
									<>
										<List dense>
											{(items as any).map((member: any) => (
												<ChatMember
													key={member.id}
													member={member}
													isAdminManager={isAdminManager}
													isOwner={isOwner}
													showChatEditSettingModal={showChatEditSettingModal}
													setOpen={setOpen}
													currentMember={currentMember}
													areAddedMembers
												/>
											))}
										</List>
										{isFetched &&
											meta?.current_page &&
											meta?.last_page &&
											meta?.current_page < meta?.last_page && (
												<LoadMoreVisible setPage={() => setPage((prev) => prev + 1)} />
											)}
										{!isFetched ? (
											<div className="case-chat-loader flex items-center justify-center">
												<Loader />
											</div>
										) : null}
										<ConfirmModal
											featureHandler={() => removeMember(open)}
											isOpen={Boolean(open)}
											toggleHandler={closeRemoveMemberHandler}
											title="Are you sure to remove the member from group chat?"
											hasLoader
											isLoading={loading}
										/>

										<WhiteModal
											handleModalOpen={() => showChatEditSettingModal(false)}
											open={chatEditSettingModal}
											maxWidth={310}
											minWidth={310}
										>
											<MemberChatSettings
												companyId={companyId}
												chatId={entityId}
												isAdminManager={isAdminManager}
												isOwner={isOwner}
												member={chatEditSettingModal}
												staffMembersLength={staffMembersLength}
											/>
										</WhiteModal>
									</>
								</div>
							</Scrollbars>
						</>
					);
				}}
			</EntityContainer.All>
		</div>
	);
};

export default function MembersListWithErrorBoundary(props: any) {
	return (
		<ErrorBoundary>
			<MembersList {...props} />
		</ErrorBoundary>
	);
}
