import React, { useEffect, useState } from 'react';
import { KntInputField } from '../../../shared/components/Fields';
import { Field } from 'formik';
import { Button, FileUploader, KntButton, Loader } from '../../../shared/components';
import get from 'lodash/get';
import { AiOutlineCloseCircle } from 'react-icons/ai';
import config from 'config';
const imgPath = `${config.PUBLIC_ASSETS}img`;

type Props = {
	onSuccess: () => void;
	setFieldValue: any;
	isSubmitting: boolean;
	values: any;
	isEditing?: any;
};

export function GroupFormElements({
	onSuccess,
	setFieldValue,
	isSubmitting,
	values,
	isEditing = false
}: Props) {
	const [uploadMeta, setUploadMeta] = useState<any>(null);
	const [cancelSending, setCancelSending] = useState<any>(false);
	const [file, setFile] = useState(isEditing ? values.image : null);

	useEffect(() => {
		if (file) setFieldValue('image', file);
	}, [file]);

	return (
		<div>
			<h2 className="mb-4 text-xl font-bold">{`${isEditing ? 'Edit' : 'Create'} Group Chat`}</h2>

			<Field
				label="Group chat title"
				labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
				name="title"
				component={KntInputField}
				placeholder="Group chat title"
				required
				autoComplete="off"
				maxLength={100}
				// onChangeHandler={(e: React.ChangeEvent<HTMLInputElement>) => {
				// 	titleRef.current = e.target.value;
				// }}
			/>

			<Field
				label="Group description"
				labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
				placeholder="Type Group Description"
				name="description"
				component={KntInputField}
				type="textarea"
				autoComplete="off"
				maxLength={250}
			/>
			<div className="mb-4 flex items-center">
				<div className="mr-4">
					<FileUploader
						uploadMeta={uploadMeta}
						setUploadMeta={setUploadMeta}
						cancelSending={cancelSending}
						setCancelSending={setCancelSending}
						setFile={setFile}
					/>
				</div>
				<div className="user-avatar-box__display relative h-20 w-20 overflow-hidden rounded-full">
					<img
						src={get(file, 'thumbnail_150') || `${imgPath}/chat-placeholder.svg`}
						alt=""
						className={'h-full object-cover'}
					/>
					{uploadMeta !== null && (
						<div
							className={
								'absolute top-1/2 left-1/2 z-10 h-10 w-10 -translate-y-1/2 -translate-x-1/2'
							}
						>
							<div className="upload-media-spinner">
								<Loader variant="determinate" value={uploadMeta} />
							</div>
							<div
								onClick={() => {
									setCancelSending(true);
									setUploadMeta(null);
								}}
								className={
									'absolute top-[21px] left-[25px] z-20 h-8 w-8 -translate-y-1/2 -translate-x-1/2 ' +
									'cursor-pointer text-3xl text-primary-dark transition hover:text-primary'
								}
							>
								<AiOutlineCloseCircle />
							</div>
						</div>
					)}
				</div>
			</div>
			<div className="btn-row mt-6 flex justify-end">
				{isSubmitting && <Loader tiled withOverlay />}
				<Button
					className={'mr-5 border border-kgrey text-kgrey'}
					type="button"
					onClickHandler={onSuccess}
				>
					Cancel
				</Button>
				<KntButton
					type="submit"
					className={'ml-4 dark:text-white dark:hover:bg-primary-300 dark:focus:ring-primary-300'}
					color="primary"
					isDisabled={isSubmitting || !!uploadMeta}
				>
					{isEditing ? 'Edit Group Chat' : 'Create Group Chat'}
				</KntButton>
			</div>
		</div>
	);
}
