import React from 'react';
import EntityForm from 'modules/entity/forms';
import { useAppState } from '../../../shared/state';
import { GroupFormElements } from './GroupFormElements';
import { useQueryParams } from '../../../shared/hooks';

type Props = {
	onSuccess: () => void;
};

export function CreateGroupForm({ onSuccess }: Props) {
	const { companyId, groupSearchQuery } = useAppState();
	const { changeUrlParams } = useQueryParams<{ chat: number }>();

	return (
		<EntityForm.Main
			entity={'ims-chats'}
			name={`AllImsChats-${companyId}-${groupSearchQuery}`}
			url={`/${companyId}/ims/groups/`}
			method="post"
			primaryKey="id"
			sendAsFormData={false}
			normalizeData={(data: any) => data}
			prependData
			fields={[
				{
					name: 'title',
					required: true,
					value: '',
					onSubmitKey: ''
				},
				{
					name: 'description',
					value: '',
					onSubmitKey: ''
				},
				{
					name: 'image',
					onSubmitKey: '',
					type: 'object'
				}
			]}
			onSuccess={(response, resetForm): any => {
				changeUrlParams({ chat: response.id });
				onSuccess();
				resetForm();
			}}
			onError={(error, resetForm): any => {
				console.log(error);
			}}
		>
			{({ isSubmitting, values, setFieldValue }) => {
				if (process.env.NODE_ENV === 'development') {
					console.log('group create values', values);
				}

				return (
					<GroupFormElements
						onSuccess={onSuccess}
						isSubmitting={isSubmitting}
						setFieldValue={setFieldValue}
						values={values}
					/>
				);
			}}
		</EntityForm.Main>
	);
}
