import React, { useState } from 'react';
import { Scrollbars } from 'react-custom-scrollbars-2';
import { useDebounce } from 'use-debounce';
import EntityContainer from 'modules/entity/containers';
import { useChatContext } from 'shared/contexts';
import { useAppState } from 'shared/state';
import { GroupMember } from 'shared/interfaces';
import { AvatarSized } from 'shared/components/Elements';
import { ErrorBoundary, LoadMoreVisible } from 'shared/components';
import ContentLoader from 'react-content-loader';

export function GroupChatMembersInner() {
	const [query, setQuery] = useState('');
	const [page, setPage] = useState(1);
	const [searchQuery] = useDebounce(query, 300);
	const { entityId } = useChatContext();
	const { companyId } = useAppState();
	return (
		<div className={'group-members'}>
			<h2 className={'mb-4 text-xl font-bold'}>Group chat members</h2>
			<div className="mb-4">
				<input
					type="text"
					className={
						'inline-block w-full rounded-xl border p-2 outline-none hover:border-primary focus:border-primary dark:bg-kdark-lighter dark:text-white'
					}
					value={query}
					onChange={(e) => setQuery(e.target.value)}
					placeholder={'Search members'}
				/>
			</div>
			<div className="group-members__content h-48">
				<Scrollbars autoHide style={{ height: '100%' }}>
					<EntityContainer.All
						entity={`group-members`}
						name={`GroupMembers-${entityId}`}
						url={`/${companyId}/ims/${entityId}/members_list/`}
						params={{
							extra: {
								search: searchQuery
							},
							page,
							limit: 10
						}}
						appendData
					>
						{({ items, isFetched, meta }) => {
							const members = [...items] as GroupMember[];
							return (
								<>
									{members.map((item) => {
										return (
											<div className="msg-info__content-user mb-3 flex items-center" key={item.id}>
												<div className="avatar mr-3 flex-shrink-0">
													<AvatarSized
														size={36}
														smallSize={36}
														className={''}
														letter={`${item.user.first_name[0]}${item.user.last_name[0]}`}
														alt={`${item.user.first_name} ${item.user.last_name}`}
														src={
															item.user.avatar?.thumbnail_150 ||
															item.user.avatar?.thumbnail ||
															item.user.avatar?.url
														}
														// color={item.user.color}
													/>
												</div>
												<div className="msg-info__user-content">
													<div className="msg-info__user-name mb-1 text-sm">
														<span>{`${item.user.first_name} ${item.user.last_name}`}</span>
														{item.role === 'OWNER' && (
															<span className="italic text-kgrey"> Group owner</span>
														)}
													</div>
												</div>
											</div>
										);
									})}
									{!isFetched
										? [...Array(6)].map((item, index) => (
												<div className={'mb-2'} key={index}>
													<MembersSkeleton />
												</div>
										  ))
										: null}
									{isFetched &&
									meta?.current_page &&
									meta?.last_page &&
									meta?.current_page < meta?.last_page ? (
										<LoadMoreVisible setPage={() => setPage((prev) => prev + 1)} />
									) : null}
								</>
							);
						}}
					</EntityContainer.All>
				</Scrollbars>
			</div>
		</div>
	);
}

export function GroupChatMembers() {
	return (
		<ErrorBoundary>
			<GroupChatMembersInner />
		</ErrorBoundary>
	);
}

function MembersSkeleton() {
	return (
		<ContentLoader
			speed={2}
			width={369}
			height={40}
			viewBox="0 0 369 40"
			backgroundColor="#f3f3f3"
			foregroundColor="#ecebeb"
		>
			<circle cx="75" cy="125" r="27" />
			<circle cx="30" cy="20" r="20" />
			<rect x="55" y="10" rx="4" ry="4" width="69" height="18" />
			<rect x="131" y="10" rx="4" ry="4" width="151" height="18" />
		</ContentLoader>
	);
}
