import React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import PeopleIcon from '@material-ui/icons/People';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import ChatMembersList from './ChatMembersList';
import AddChatMembers from './AddChatMembers';
import { useAppState } from '../../../../shared/state';
import { useChatContext } from '../../../../shared/contexts';
import { useGetEntityOne } from '../../../../shared/hooks';
import get from 'lodash/get';
import { ChatMember } from '../../types';
import { useAuth } from '../../../../modules/auth/hooks/useAuth';

function TabPanel(props: any) {
	const { children, value, index, ...other } = props;

	return (
		<div
			role="tabpanel"
			hidden={value !== index}
			id={`scrollable-force-tabpanel-${index}`}
			aria-labelledby={`scrollable-force-tab-${index}`}
			{...other}
		>
			{value === index && (
				<Box p={0}>
					<Typography component="div">{children}</Typography>
				</Box>
			)}
		</div>
	);
}

TabPanel.propTypes = {
	children: PropTypes.node,
	index: PropTypes.any.isRequired,
	value: PropTypes.any.isRequired
};

function a11yProps(index: number) {
	return {
		id: `scrollable-force-tab-${index}`,
		'aria-controls': `scrollable-force-tabpanel-${index}`
	};
}

type Props = {
	onSuccess: () => void;
};

const Members = ({ onSuccess }: Props) => {
	const { isPermitted, companyId } = useAppState();
	const { entityId } = useChatContext();
	const { userData } = useAuth();
	const [value, setValue] = React.useState(0);

	const chatEntity = useGetEntityOne({
		entity: 'ims-chats',
		entityId,
		entityName: `GroupChat-${entityId}One`
	});

	const activeChat = chatEntity.item || {};

	const owner = (get(chatEntity, 'item.members', []) as ChatMember[]).find(
		(member) => member.role === 'OWNER'
	);
	const currentMember = (get(chatEntity, 'item.members', []) as ChatMember[]).find(
		(member) => member.user.id === userData.id
	);
	const isOwner = owner?.user.id === userData.id;

	const isAllowed = currentMember?.is_allowed_add_user || isPermitted;

	const handleChange = (event: React.ChangeEvent<Record<string, unknown>>, newValue: any) => {
		setValue(newValue);
	};

	return (
		<div className="case-form case-form_group-chat -mt-5 rounded-xl">
			<AppBar position="static" color="default">
				<Tabs
					value={value}
					onChange={handleChange}
					variant="fullWidth"
					indicatorColor="primary"
					textColor="primary"
					aria-label="scrollable2 force tabs example"
				>
					<Tab label="Members" icon={<PeopleIcon />} {...a11yProps(0)} />
					<Tab
						label="Add Members"
						icon={<PersonAddIcon />}
						{...a11yProps(1)}
						disabled={!isAllowed}
					/>
				</Tabs>
			</AppBar>
			<TabPanel value={value} index={0}>
				<ChatMembersList
					isAdminManager={isPermitted}
					isOwner={isOwner}
					currentMember={currentMember}
					staffMembersLength={123}
				/>
			</TabPanel>
			<TabPanel value={value} index={1}>
				<AddChatMembers
					{...{ chatId: entityId, companyId, onSuccess, activeChat, staffMembersLength: 123 }}
				/>
			</TabPanel>
		</div>
	);
};

export default Members;
