import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import { deepPurple } from '@material-ui/core/colors';
import { makeStyles } from '@material-ui/core/styles';
import get from 'lodash/get';
import classnames from 'classnames';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import IconButton from '@material-ui/core/IconButton';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';

const useStyles = makeStyles((theme) => ({
	root: {
		display: 'flex',
		'& > *': {
			margin: theme.spacing(1)
		}
	},
	purple: {
		color: theme.palette.getContrastText(deepPurple[500]),
		backgroundColor: deepPurple[500]
	}
}));

type Props = {
	member: any;
	areAddedMembers?: boolean;
	onClickHandler?: (id: any, member: any) => void;
	selectedMembers?: any;
	isAddingMember?: boolean;
	isOwner?: boolean;
	isAdminManager?: boolean;
	showChatEditSettingModal?: (member: any) => void;
	setOpen?: any;
	currentMember?: any;
};

const ChatMember = ({
	member,
	areAddedMembers,
	onClickHandler = () => {},
	selectedMembers = [],
	isAddingMember = false,
	isOwner = false,
	isAdminManager,
	showChatEditSettingModal = () => {},
	setOpen,
	currentMember
}: Props) => {
	const classes = useStyles();
	const firstName = isAddingMember ? member.first_name : get(member, 'user.first_name');
	const lastName = isAddingMember ? member.last_name : get(member, 'user.last_name');
	const companyName = get(member, 'company.name');
	const isIndividualClient = isAddingMember
		? companyName === member?.username
		: companyName === get(member, 'user.username');
	const avatarSrc = isAddingMember
		? get(member, 'avatar.thumbnail_150')
		: get(member, 'user.avatar.thumbnail_150');
	const FN = firstName[0]?.toUpperCase();
	const LN = lastName[0]?.toUpperCase();
	const name = `${firstName} ${lastName}`;
	const userId = isAddingMember ? member.id : get(member, 'user.id');
	const role = !isAddingMember ? member.role : null;
	const isBusinessAdmin = !isAddingMember ? member?.is_admin : null;
	const isStaff = member?.is_staff;

	const isCurrentChatOwner = member.role === 'OWNER';
	const isMemberSelected = selectedMembers.find(
		(selectedMember: any) =>
			selectedMember.id === userId && selectedMember.companyId === member?.company?.id
	);
	const renderMemberType = () => {
		if (isStaff && isCurrentChatOwner) {
			return '';
		} else {
			return `${isStaff ? 'Staff' : 'Client'} | ${isIndividualClient ? 'Individual' : companyName}`;
		}
	};

	return (
		<ListItem
			// @ts-ignore
			button={!areAddedMembers}
			onClick={() => onClickHandler(userId, member)}
			className={classnames('member-list-item', {
				'item-active': selectedMembers.includes(userId)
			})}
		>
			<ListItemAvatar>
				<Avatar alt={FN} src={avatarSrc} className={classes.purple}>
					{`${FN} ${LN}`}
				</Avatar>
			</ListItemAvatar>
			{
				<ListItemText
					primary={name}
					secondary={`${isCurrentChatOwner ? 'GROUP OWNER' : ''} ${renderMemberType()}`}
				/>
			}
			{isMemberSelected && (
				<>
					<ListItemSecondaryAction>
						<IconButton edge="end" aria-label="checked">
							<CheckCircleOutlineIcon />
						</IconButton>
					</ListItemSecondaryAction>
				</>
			)}

			{areAddedMembers && (isAdminManager || isOwner || currentMember.is_allowed_remove_user) && (
				<ListItemSecondaryAction>
					{(isAdminManager || isOwner) && (
						<IconButton
							edge="end"
							aria-label="checked"
							disabled={role === 'OWNER' || isBusinessAdmin}
							onClick={() => showChatEditSettingModal(member)}
						>
							<EditIcon />
						</IconButton>
					)}

					<IconButton
						edge="end"
						aria-label="checked"
						disabled={role === 'OWNER' /*|| isBusinessAdmin*/}
						onClick={() => setOpen(member)}
					>
						<DeleteIcon />
					</IconButton>
				</ListItemSecondaryAction>
			)}
		</ListItem>
	);
};

export default ChatMember;
