import React, { useEffect } from 'react';
import { GroupMember, IGroupChat } from 'shared/interfaces';
import isEmpty from 'lodash/isEmpty';
import { ChatHeaderLayout } from 'shared/components/Chat/components/ChatHeaderLayout';
import { useChatMenu } from '../hooks/useChatMenu';
import { Menu } from 'shared/components/Elements';
import { useChatContext } from 'shared/contexts/ChatContext';
import { createStyles, makeStyles } from '@material-ui/core/styles';
import { Permissions } from '../components/Permissions';
import { useAuth } from 'modules/auth/hooks/useAuth';
import { useDraftMessage, useQueryParams, useUpdateEntities, useWhiteModal } from 'shared/hooks';
import { useChatSocket } from 'shared/contexts';
import { GroupChatMembers } from './GroupChatMembers';
import { useAppState } from 'shared/state';
import { ErrorBoundary } from 'shared/components';
import { IconPin } from '../../../shared/icons';
import { AiOutlineMore, AiOutlineSearch, AiOutlineUsergroupAdd } from 'react-icons/ai';
import { FiUsers } from 'react-icons/fi';
import { IoClose } from 'react-icons/io5';
import { useGroupMessageSearch } from '../hooks';
import ChatMembers from './Members/ChatMembers';
import { ChatMember } from '../types';

type Props = {
	imsData: IGroupChat;
	isFetched: boolean;
};

const useStyles = makeStyles(
	createStyles({
		paper: {
			borderRadius: '16px',
			padding: '10px'
		}
	})
);

export function GroupChatHeaderInner({ imsData, isFetched }: Props) {
	const { setScrollToMessageHandler, lastReadMessageId } = useChatContext();
	const { userData } = useAuth();
	const classes = useStyles();
	const { removeQueryParams } = useQueryParams();
	const { closeSocket } = useChatSocket();
	const { setActiveDraft, activeDraft, setGroupSearchOpen, groupSearchOpen, isAdmin } =
		useAppState();
	const { saveDraft } = useDraftMessage();
	const { toggleSearchSidebar } = useGroupMessageSearch();
	const pinTime = imsData.pin_time;
	const {
		open: membersModalOpen,
		modalHandler: membersModalOpenHandler,
		WhiteModal
	} = useWhiteModal();

	const { updateEntities } = useUpdateEntities();

	const {
		open: membersAddModalOpen,
		modalHandler: membersAddModalOpenHandler,
		WhiteModal: MembersAddWhiteModal
	} = useWhiteModal();

	const { open, handleClick, anchorEl, handleCloseMenu } = useChatMenu(
		imsData.pin_time,
		imsData.id,
		true
	);

	const mainText = imsData.title;
	let headerAvatar = null;

	if (isFetched && !isEmpty(imsData)) {
		headerAvatar = {
			avatarSrc: imsData?.image?.thumbnail_150,
			avatarLetter: imsData?.title?.[0],
			avatarAlt: imsData?.title
		};
	}

	const currentUserMemberData = imsData?.members?.find(
		(member: GroupMember) => member?.user?.id === userData.id
	);
	const owner = (imsData?.members as ChatMember[])?.find((member) => member.role === 'OWNER');
	const isOwner = owner?.user?.id === userData.id;

	// // const isAllowedExitGroup = currentMember?.is_allowed_exit_group;
	// const isAllowedPinMsg = currentUserMemberData?.is_allowed_pin_msg;
	// const isAllowedSendMsg = currentUserMemberData?.is_allowed_send_msg;

	const closeCaseChat = () => {
		//Close message search sidebar if it is open
		if (groupSearchOpen) {
			setGroupSearchOpen(false);
		}
		removeQueryParams();
		closeSocket?.();
		if (activeDraft) saveDraft();

		try {
			updateEntities({
				entity: 'ims-chats',
				entityId: imsData?.id,
				updatingData: {
					last_read_message: lastReadMessageId
				}
			});
		} catch (e) {}
	};

	useEffect(() => {
		if ('draft_message' in imsData && imsData?.draft_message && !activeDraft) {
			setActiveDraft(imsData.draft_message);
		}
	}, [imsData]);

	return (
		<ChatHeaderLayout
			isFetched
			HeaderActions={
				<>
					{pinTime ? (
						<div className={'mr-4'}>
							<IconPin className={'text-kgrey-menuText'} />
						</div>
					) : null}

					{/*Members Popup open button*/}
					{isAdmin || isOwner || currentUserMemberData?.is_allowed_add_user ? (
						<button
							className={
								'flex-centered mr-2 h-8 w-8 rounded-full bg-primary text-white transition hover:bg-primary-dark active:translate-y-0.5 active:shadow-inner'
							}
							onClick={membersAddModalOpenHandler}
						>
							<AiOutlineUsergroupAdd />
						</button>
					) : (
						<button
							className={
								'flex-centered h-8 w-8 rounded-full bg-primary text-white transition hover:bg-primary-dark active:translate-y-0.5 active:shadow-inner'
							}
							onClick={membersModalOpenHandler}
						>
							<FiUsers />
						</button>
					)}

					{/*Group Chat Members and Add Members popup*/}
					<MembersAddWhiteModal
						handleModalOpen={membersAddModalOpenHandler}
						open={membersAddModalOpen}
						noPadding
						cropRounded
					>
						<ChatMembers onSuccess={membersAddModalOpenHandler} />
					</MembersAddWhiteModal>

					{/*Group Chat Members and Add Members popup*/}
					<WhiteModal handleModalOpen={membersModalOpenHandler} open={membersModalOpen}>
						<GroupChatMembers />
					</WhiteModal>

					{/* Group Chat Search Sidebar open button */}
					<button
						className={
							'flex-centered ml-2 h-8 w-8 rounded-full bg-primary text-white transition hover:bg-primary-dark active:translate-y-0.5 active:shadow-inner'
						}
						onClick={toggleSearchSidebar}
					>
						<AiOutlineSearch />
					</button>

					{/*Three dot button*/}
					<div
						className="more-btn w-4 cursor-pointer text-right text-kdark-dark333
						 transition hover:text-primary dark:text-white dark:hover:text-primary"
						onClick={handleClick}
					>
						<AiOutlineMore className={'inline text-3xl'} />
					</div>
					{/*Permissions popover*/}
					<Menu
						id="long-menu"
						anchorEl={anchorEl}
						keepMounted
						open={open}
						classes={{ paper: classes.paper }}
						onClose={handleCloseMenu}
					>
						{isFetched ? (
							<Permissions
								image={imsData?.image}
								title={imsData?.title}
								membersCount={imsData?.members?.length}
								isMuted={currentUserMemberData?.is_mute}
								chatId={imsData.id}
								memberId={currentUserMemberData?.id}
								isOwner={isOwner}
							/>
						) : null}
					</Menu>

					{/*Group chat close button*/}
					<button
						className="flex-center-y border-border-solid border-l px-4 py-2 text-kred-pink transition hover:opacity-80 lg:ml-4"
						type={'button'}
						onClick={closeCaseChat}
					>
						<div className="icon ">
							<IoClose />
						</div>
					</button>
				</>
			}
			mainText={mainText}
			data={headerAvatar}
			pinnedMessage={{
				title: imsData?.pinned_message?.text,
				id: imsData?.pinned_message?.id
			}}
			scrollToHandler={setScrollToMessageHandler}
		/>
	);
}

export function GroupChatHeader(props: Props) {
	return (
		<ErrorBoundary>
			<GroupChatHeaderInner {...props} />
		</ErrorBoundary>
	);
}
