import React from 'react';
import { ChatItemMarkup } from './ChatItemMarkup';
import { ChatItemProps } from '../types';
import { ErrorBoundary } from 'shared/components';

function ChatItemInner(props: ChatItemProps) {
	const {
		chatId,
		image,
		pin_time,
		last_message_type,
		last_message_text,
		title,
		last_message_author,
		last_message_time,
		isMuted,
		draftMessage,
		lastReadMessage
	} = props;

	return (
		<ChatItemMarkup
			chatId={chatId}
			title={title}
			image={image}
			last_message_author={last_message_author}
			last_message_type={last_message_type}
			last_message_text={last_message_text}
			last_message_time={last_message_time}
			pin_time={pin_time}
			isMuted={isMuted}
			draftMessage={draftMessage}
			lastReadMessage={lastReadMessage}
		/>
	);
}

export function ChatItem(props: ChatItemProps) {
	return (
		<ErrorBoundary isCompact>
			<ChatItemInner {...props} />
		</ErrorBoundary>
	);
}
