import React, { useEffect, useState } from 'react';
import { CgSearch } from 'react-icons/cg';
import { useDebounce } from 'use-debounce';
import { useAppState } from '../../../shared/state';
import { useWhiteModal } from '../../../shared/hooks';
import { CreateGroupForm } from './CreateGroupForm';
import { Fab } from '@material-ui/core';
import { IoMdAdd } from 'react-icons/io';
import { BsFillBellFill } from 'react-icons/bs';
import { AiOutlineMenuUnfold } from 'react-icons/ai';

type Props = {
	toggleIsUnreadFilter: any;
	isUnreadFilter: boolean;
};

export function GroupSidebarSearch({ toggleIsUnreadFilter, isUnreadFilter }: Props) {
	const [query, setQuery] = useState('');
	const [searchQuery] = useDebounce(query, 500);
	const { setGroupSearchQuery, groupsUnreadCountData } = useAppState();
	const { open, modalHandler, WhiteModal } = useWhiteModal();
	const totalGroupUnreadCount = Object.values(groupsUnreadCountData)?.filter(
		(count) => count > 0
	).length;

	useEffect(() => {
		setGroupSearchQuery(searchQuery);
	}, [searchQuery]);

	return (
		<div className="mb-4 flex">
			{totalGroupUnreadCount ? (
				<div
					onClick={() => toggleIsUnreadFilter()}
					className="relative mr-3 mt-1.5 cursor-pointer rounded-10 bg-kgrey-dark-2 p-2">
					{!isUnreadFilter ? (
						<>
							<span className="absolute -top-1 left-1.5 right-1.5 rounded-md bg-kred-pink text-center text-10px text-white">
								{totalGroupUnreadCount}
							</span>

							<BsFillBellFill />
						</>
					) : (
						<AiOutlineMenuUnfold />
					)}
				</div>
			) : null}

			<div className="group-sidebar__search relative flex-grow  pr-2">
				<input
					type="search"
					value={query}
					className={
						'group-sidebar__search-input w-full border-b outline-none ' +
						'border-kgrey-border-solid' +
						' pl-7 transition focus:border-primary' +
						' placeholder-[rgba(45, 45, 45, 0.3)] py-2 dark:bg-kdark dark:text-white'
					}
					placeholder={'Search for chats'}
					onChange={(e) => setQuery(e.target.value)}
				/>
				<div className="absolute top-1/2 left-0 -translate-y-1/2">
					<div className="text-kgrey">
						<CgSearch />
					</div>
				</div>
			</div>
			<Fab color="primary" size="small" onClick={modalHandler}>
				<IoMdAdd className="text-2xl text-white" />
			</Fab>
			<WhiteModal handleModalOpen={modalHandler} open={open}>
				<CreateGroupForm onSuccess={modalHandler} />
			</WhiteModal>
		</div>
	);
}
