import React from 'react';
import EntityContainer from 'modules/entity/containers';
import { useGetEntity, useNotistack, useQueryParams } from 'shared//hooks';
import { useAppState } from 'shared/state';
import { GroupMember, IGroupChat } from 'shared/interfaces';
import { ChatLayout } from 'shared/components/Chat/ChatLayout';
import { GroupMessagesList } from './GroupMessagesList';
import { GroupChatInputControls } from './GroupChatInputControls';
import { ChatContextProvider, ChatInputContextProvider, ChatSocketProvider } from 'shared/contexts';
import { GroupChatHeader } from './GroupChatHeader';
import { useAuth } from 'modules/auth/hooks/useAuth';
import { ErrorBoundary } from 'shared/components';

type Props = {
	isUnreadFilterActive: boolean;
};

function GroupChatInner({ isUnreadFilterActive }: Props) {
	const { query, removeQueryParams } = useQueryParams<{ chat: number }>();
	const { showNotification } = useNotistack();
	const { companyId, groupSearchQuery } = useAppState();
	const chatId = Number('chat' in query ? query.chat : undefined);
	const { userData } = useAuth();
	const { all: groupList } = useGetEntity({
		entity: 'ims-chats',
		entityName: `AllImsChats-${companyId}-${groupSearchQuery}`
	});

	return (
		<>
			{chatId && companyId ? (
				<EntityContainer.One
					id={String(chatId)}
					relations={''}
					entity={'ims-chats'}
					name={`GroupChat-${chatId}`}
					url={`/${companyId}/ims/groups/${chatId}/`}
					params={{}}
					onError={(error) => {
						//if current user does not have access to the chat, It should be closed
						if (error?.response?.status === 404) {
							removeQueryParams();
							showNotification({ variant: 'error', message: "You don't have access to the chat." });
						}
					}}
				>
					{({ item, isFetched }) => {
						const imsData = { ...item } as IGroupChat;

						const currentUserMemberData = imsData?.members?.find(
							(member: GroupMember) => member?.user?.id === userData.id
						);

						return (
							<ChatContextProvider entityId={chatId} entityType={'ims'}>
								<ChatInputContextProvider>
									<ChatSocketProvider entityId={chatId} entityType={'ims'}>
										<ChatLayout
											ChatHeader={<GroupChatHeader isFetched={isFetched} imsData={imsData} />}
											ChatBody={<GroupMessagesList chatId={chatId} />}
											ChatInput={
												<GroupChatInputControls
													isAllowedSendMsg={currentUserMemberData?.is_allowed_send_msg}
												/>
											}
										/>
									</ChatSocketProvider>
								</ChatInputContextProvider>
							</ChatContextProvider>
						);
					}}
				</EntityContainer.One>
			) : (
				<div className={'flex-centered h-full w-full text-xl font-semibold dark:text-white'}>
					{groupList?.items?.length
						? 'Please select group chat'
						: `You have ${isUnreadFilterActive ? 'no unread' : 'no'} group chats`}
				</div>
			)}
		</>
	);
}

export function GroupChat(props: Props) {
	return (
		<ErrorBoundary>
			<GroupChatInner {...props} />
		</ErrorBoundary>
	);
}
