import React from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import Divider from '@material-ui/core/Divider';
import Highlighter from 'react-highlight-words';
import { normalizeSearchResult } from '../../../shared/components/LastMessageView';
import { IMessage } from '../../../shared/interfaces';

type Props = {
	message: IMessage;
	listItemClickHandler: (messageId: number) => void;
	searchQuery: string;
};

function SearchMessageItem({ message, listItemClickHandler, searchQuery }: Props) {
	const name = `${message.from_user?.first_name} ${message.from_user?.last_name}`;
	const avatarSrc = message?.from_user?.avatar?.thumbnail_150;
	return (
		<>
			<ListItem
				onClick={() => listItemClickHandler(message.message_id!)}
				className="hover:bg-kprimary-200 cursor-pointer transition"
			>
				<ListItemAvatar>
					<Avatar alt={name} src={avatarSrc} />
				</ListItemAvatar>
				<ListItemText
					className="dark:text-white"
					primary={name}
					secondary={<MessageText messageText={message?.text || ''} searchQuery={searchQuery} />}
				/>
			</ListItem>
			<Divider variant="inset" component="li" />
		</>
	);
}

type MessageTextType = {
	searchQuery: string;
	messageText: string;
};

function MessageText({ searchQuery, messageText }: MessageTextType) {
	// const splittedWords =
	// 	searchQuery && !isEmpty(searchQuery) && searchQuery.toLowerCase().split(/\s/);
	// const searchWords = [isArray(splittedWords) && splittedWords.join(' ')] || [];
	if (searchQuery) {
		const searchWords = searchQuery.split(/\s/).filter((word: string) => word);
		const normalizedText = '' + normalizeSearchResult(messageText, searchQuery, 25);

		return (
			<span>
				<Highlighter
					highlightClassName="highlighted"
					searchWords={searchWords}
					textToHighlight={normalizedText}
				/>
			</span>
		);
	}

	return <span>{messageText}</span>;
}

export default SearchMessageItem;
