/**@jsxImportSource @emotion/react */
import React from 'react';
import clsx from 'clsx';
import { css } from '@emotion/react';
import { nanoid } from 'nanoid';
import { LastMessageView } from 'shared/components/LastMessageView';
import { time } from 'shared/services';
import { IconVertical } from 'shared/icons/IconVertical';
import { Menu } from 'shared/components/Elements';
import MenuItem from '@material-ui/core/MenuItem';
import { IconGroupChatMuted, IconPin } from 'shared/icons';
import { useQueryParams } from 'shared/hooks';
import { useAppState } from 'shared/state';
import config from 'config';
import { useChatMenu } from '../hooks/useChatMenu';
import { ChatItemProps } from '../types';
import { SiMaildotru } from 'react-icons/si';
import { PiClockClockwise } from 'react-icons/pi';
const imgPath = `${config.PUBLIC_ASSETS}img/`;

export function ChatItemMarkup({
	chatId,
	image,
	title,
	last_message_author,
	last_message_type,
	last_message_text,
	last_message_time,
	pin_time,
	draftMessage,
	isMuted = false,
	lastReadMessage = null
}: ChatItemProps) {
	const {
		groupsUnreadCountData,
		setActiveDraft,
		chatsMentionList,
		scheduledMessageIds,
		setChatUuid
	} = useAppState();
	const { changeUrlParams, query } = useQueryParams<{ chat: number; searchedMessageId?: number }>();

	const { open, handleClick, anchorEl, handleMenuItemClick, handleCloseMenu } = useChatMenu(
		pin_time,
		chatId
	);

	const isActive = query && Number(query.chat) === chatId;
	const chatAvatar = image || `${imgPath}chat-placeholder.png`;
	const hasUnread = groupsUnreadCountData?.[chatId];
	const hasMentions = chatsMentionList?.[chatId]?.length;
	const hasScheduledMessages = scheduledMessageIds?.[chatId]?.length;

	const chatItemClickHandler = () => {
		changeUrlParams({
			chat: chatId,
			...{
				...(lastReadMessage && hasUnread
					? {
							unread: lastReadMessage,
							hasUnreadMessage: true,
							lastReadMessage
					  }
					: {})
			}
		});
		setChatUuid(nanoid());
		setActiveDraft(draftMessage);
	};

	return (
		<div
			className={clsx('chat-item mb-3 cursor-pointer rounded-lg p-2 transition hover:opacity-80 ', {
				'bg-primary-300 dark:bg-primary-300': isActive,
				'dark:bg-white/10': !isActive
			})}
		>
			<div className="chat-item__inner flex w-full items-center justify-between">
				<div className="chat-item__img h-14 w-14 flex-shrink-0 overflow-hidden rounded-full">
					<img src={chatAvatar} alt="" className="h-full object-cover" />
				</div>
				<div
					className="chat-item__body flex flex-grow items-center justify-between pl-2"
					css={css`
						width: 275px;
					`}
				>
					<div
						className="chat-item__content w-9/12 overflow-hidden pr-2"
						onClick={chatItemClickHandler}
					>
						<div className="chat-item__title flex-center-y truncate text-sm font-semibold dark:text-white">
							<span>{title}</span>
							{isMuted && (
								<span className="ml-1 block h-4 w-4 text-kgrey">
									<IconGroupChatMuted />
								</span>
							)}
						</div>
						<div className="chat-item__muted">
							{last_message_author ? (
								<LastMessageView
									last_message_type={last_message_type}
									last_message_text={last_message_text || ''}
									last_message_author={last_message_author}
									draftMessage={draftMessage}
								/>
							) : null}
						</div>
					</div>
					<div className="chat-item__info  flex w-3/12 flex-shrink-0 flex-col  items-end">
						<div className="chat-item___actions mb-2.5 flex items-center justify-between">
							<div className="chat-item__date text-[10px] font-bold text-kgrey-chatGrey/30 dark:text-white">
								{time.toLastTime(last_message_time)}
							</div>
							<div className="chat-item__dots">
								<div
									className="more-btn w-4 cursor-pointer text-right text-kgrey-menuText transition-colors hover:text-primary dark:text-white dark:hover:text-primary"
									onClick={handleClick}
								>
									<IconVertical className={'inline'} />
								</div>
								<Menu
									id="long-menu"
									anchorEl={anchorEl}
									keepMounted
									open={open}
									onClose={handleCloseMenu}
								>
									<MenuItem
										onClick={() => {
											handleCloseMenu();
											handleMenuItemClick(pin_time ? 'unpin' : 'pin');
										}}
									>
										{`${pin_time ? 'Unpin' : 'Pin'} Chat`}
									</MenuItem>
								</Menu>
							</div>
						</div>
						<div className="chat-item___pin mt-auto flex items-center justify-end leading-none">
							{hasScheduledMessages ? (
								<div className="chat-item__scheduled-icon mr-1.5 items-start text-xl dark:text-white">
									<PiClockClockwise />
								</div>
							) : null}
							{pin_time ? <IconPin className={'text-kgrey-menuText'} /> : null}
							{hasUnread ? (
								<div
									className={clsx(
										'ml-2 h-6 w-6  rounded-full text-center text-sm font-semibold leading-6 text-white',
										{
											'bg-primary': !isMuted,
											'bg-kgrey': isMuted
										}
									)}
								>
									{hasUnread}
								</div>
							) : null}
							{hasMentions ? (
								<div className=" px-1 text-lg text-primary">
									<SiMaildotru />
								</div>
							) : null}
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}
