import React from 'react';
import { GroupImage } from '../../../shared/interfaces';
import { GroupFormElements } from './GroupFormElements';
import EntityForm from '../../../modules/entity/forms';
import { useAppState } from '../../../shared/state';

type Props = {
	onSuccess: () => void;
	title: string;
	description: string;
	image: null | GroupImage;
	id: number;
};

export function EditGroupForm({ onSuccess, title, description, image, id }: Props) {
	const { groupSearchQuery, companyId } = useAppState();

	return (
		<>
			<EntityForm.Main
				entity={'ims-chats'}
				name={`AllImsChats-${companyId}-${groupSearchQuery}`}
				url={`/${companyId}/ims/groups/${id}/`}
				id={id}
				method="put"
				primaryKey="id"
				sendAsFormData={false}
				normalizeData={(data: any) => data}
				prependData
				fields={[
					{
						name: 'title',
						required: true,
						value: title,
						onSubmitKey: ''
					},
					{
						name: 'description',
						value: description,
						onSubmitKey: ''
					},
					{
						name: 'image',
						onSubmitKey: '',
						type: 'object',
						value: image
					}
				]}
				onSuccess={(response, resetForm): any => {
					onSuccess();
					resetForm();
				}}
				onError={(error, resetForm): any => {
					console.log(error);
				}}
			>
				{({ isSubmitting, values, setFieldValue }) => {
					if (process.env.NODE_ENV === 'development') {
						console.log('group edit values', values);
					}

					return (
						<GroupFormElements
							onSuccess={onSuccess}
							isSubmitting={isSubmitting}
							setFieldValue={setFieldValue}
							values={values}
							isEditing
						/>
					);
				}}
			</EntityForm.Main>
		</>
	);
}
