/** @jsxImportSource @emotion/react */
import React, { useCallback, useEffect, useState } from 'react';
import { GroupSidebarSearch } from './components/GroupSidebarSearch';
import { GroupList } from './components/GroupList';
import { GroupChat } from './chat-components/GroupChat';
import { useGroupInitialSettings } from './hooks/useGroupInitialSettings';
import { useQueryParams, useUnreadCaseTab, useWindowSize } from '../../shared/hooks';
import clsx from 'clsx';
import { isMobile } from 'react-device-detect';
import { Helmet } from 'react-helmet';
import { ErrorBoundary } from '../../shared/components';
import { useAppState } from '../../shared/state';
import { GroupMessageSearch } from './components';
import { Scrollbars } from 'react-custom-scrollbars-2';
// import { GroupSidebarTop } from './components/GroupSidebarTop';
// import { GroupContextProvider } from './contexts/GroupContext';

const PAGE_ELEM_WIDTHS = {
	APP_SIDEBAR: 285,
	APP_CONTAINER: 1155,
	APP_CONTAINER_CONTENT_PADDING_LEFT: 28,
	CHAT_SIDEBAR: 362,
	CHAT_SIDEBAR_DIFFERENCE_ON_SEARCH_OPEN: 77,
	CHAT_SIDEBAR_MARGIN_RIGHT: 8,
	GROUP_SEARCH_SIDEBAR: 285
};

const {
	APP_SIDEBAR,
	APP_CONTAINER_CONTENT_PADDING_LEFT,
	CHAT_SIDEBAR,
	CHAT_SIDEBAR_MARGIN_RIGHT,
	GROUP_SEARCH_SIDEBAR,
	CHAT_SIDEBAR_DIFFERENCE_ON_SEARCH_OPEN
} = PAGE_ELEM_WIDTHS;

function GroupsInner() {
	useGroupInitialSettings();
	const { unreadCasesQuantity, isHidden } = useUnreadCaseTab('Group chats');
	const { query } = useQueryParams();
	const { groupSearchOpen, groupsUnreadCountData, isDarkMode } = useAppState();
	const { width: windowWidth } = useWindowSize();
	const [isUnreadFilter, setIsUnreadFilter] = useState(false);

	const toggleIsUnreadFilter = useCallback(() => {
		setIsUnreadFilter((prev) => !prev);
	}, []);

	useEffect(() => {
		if (
			isUnreadFilter &&
			!Object.values(groupsUnreadCountData)?.filter((count) => count > 0).length
		) {
			setIsUnreadFilter(false);
		}
	}, [groupsUnreadCountData]);

	const COMPUTED_CHAT_WIDTH =
		windowWidth! -
		APP_SIDEBAR -
		APP_CONTAINER_CONTENT_PADDING_LEFT -
		CHAT_SIDEBAR -
		CHAT_SIDEBAR_MARGIN_RIGHT -
		GROUP_SEARCH_SIDEBAR;

	const COMPUTED_CHAT_WIDTH_NORMAL =
		windowWidth! -
		APP_SIDEBAR -
		APP_CONTAINER_CONTENT_PADDING_LEFT -
		CHAT_SIDEBAR -
		CHAT_SIDEBAR_MARGIN_RIGHT;

	return (
		<div /*ref={casePageRef}*/ className={'flex h-full w-full flex-col  pl-2 pb-1 lg:pl-7 lg:pt-7'}>
			<div className="relative flex h-full">
				{(!isHidden || unreadCasesQuantity === 0) && (
					<Helmet>
						<title>KENNEKT | Group chats</title>
					</Helmet>
				)}
				<div
					className={clsx('w-full flex-shrink-0 transition-all lg:mr-2', {
						hidden: query.chat && isMobile,
						'lg:w-[362px]': !groupSearchOpen,
						'lg:w-[285px]': groupSearchOpen
					})}
				>
					<div className="group-sidebar flex h-full flex-col pt-4 font-poppins lg:pt-0">
						{/*<GroupSidebarTop />*/}
						<GroupSidebarSearch
							isUnreadFilter={isUnreadFilter}
							toggleIsUnreadFilter={toggleIsUnreadFilter}
						/>
						<GroupList isUnreadFilter={isUnreadFilter} />
					</div>
				</div>
				<div
					className={clsx(!groupSearchOpen && 'flex-grow', 'transition-all')}
					style={{
						width: groupSearchOpen
							? `${COMPUTED_CHAT_WIDTH + CHAT_SIDEBAR_DIFFERENCE_ON_SEARCH_OPEN}px`
							: COMPUTED_CHAT_WIDTH_NORMAL
					}}
				>
					<GroupChat isUnreadFilterActive={isUnreadFilter} />
				</div>
				<div
					className={clsx(
						`message-search bottom-o absolute top-0 right-0 w-[285px] bg-white transition`,
						{
							'translate-x-[285px]': !groupSearchOpen,
							'translate-x-0': groupSearchOpen
						}
					)}
				>
					<GroupMessageSearch />
					<span className=" hidden focus:ring-2 focus:ring-primary" />
				</div>
			</div>
		</div>
	);
}

export default function Groups() {
	return (
		<ErrorBoundary>
			<GroupsInner />
		</ErrorBoundary>
	);
}
