import { useAxios, useGetEntityOne, useQueryParams, useUpdateEntities } from 'shared/hooks';
import { useAppState } from 'shared/state';
import { useEntityForm } from 'modules/entity/hooks/useEntityForm';
import { Dispatch, SetStateAction, useCallback, useEffect, useRef, useState } from 'react';
import { BiMessageSquareDetail } from 'react-icons/bi';
import { CgAdd } from 'react-icons/cg';
import { MdOutlineRemoveCircleOutline } from 'react-icons/md';
import { AiOutlineEdit, AiOutlinePushpin } from 'react-icons/ai';
import { IoExitOutline } from 'react-icons/io5';
import get from 'lodash/get';
import { ChatMember } from '../types';
import { useAuth } from '../../../modules/auth/hooks/useAuth';
import { GroupMember, IGroupChat } from '../../../shared/interfaces';

type GroupRequests = {
	chatId: number;
	isMuted?: boolean | undefined;
	memberId?: number | undefined;
	mutingCallback?: (chatId: number, data: { [key: string]: any }) => void;
	setExitLoading?: Dispatch<SetStateAction<boolean>>;
	setExitOpen?: Dispatch<SetStateAction<boolean>>;
};

export function useGroupRequest({ chatId, isMuted, memberId }: GroupRequests) {
	const { companyId, groupSearchQuery, isAdmin } = useAppState();
	const { userData } = useAuth();
	const { removeQueryParams, query } = useQueryParams();

	const [exitLoading, setExitLoading] = useState(false);
	const [open, setExitOpen] = useState(false);

	const [deleteVisible, setDeleteVisible] = useState(false);
	const [deleteLoading, setDeleteLoading] = useState(false);

	const toggleDeleteVisible = () => setDeleteVisible((prev) => !prev);
	const toggleDeleteLoading = () => setDeleteLoading((prev) => !prev);

	const propertyRef = useRef<null | IGroupChat>(null);

	const { updateEntities } = useUpdateEntities();

	const entityId = query?.searchedMessageId ? query?.searchedMessageId : chatId;

	const chatEntity = useGetEntityOne({
		entity: 'ims-chats',
		entityId: chatId,
		entityName: `GroupChat-${entityId}One`
	});

	useEffect(() => {
		propertyRef.current = chatEntity.item.property;
	}, [chatEntity.item.property]);

	const owner = (get(chatEntity, 'item.members', []) as ChatMember[]).find(
		(member) => member.role === 'OWNER'
	);
	const currentMember = (get(chatEntity, 'item.members', []) as ChatMember[]).find(
		(member) => member.user.id === userData.id
	);
	// const isAllowedExitGroup = currentMember?.is_allowed_exit_group;
	const isOwner = owner?.user.id === userData.id;

	let canLeaveChat = true;
	if (isOwner || isAdmin || !currentMember?.is_allowed_exit_group) canLeaveChat = false;

	let canDeleteChat = false;
	if (isAdmin || isOwner || currentMember?.is_allowed_delete_group) canDeleteChat = true;

	//Update Permission accept muted and delete chat
	const { fetchData: updatePermission } = useAxios({
		url: `/${companyId}/ims/groups/${chatId}/`,
		dynamicValues: true,
		method: 'put',
		cb: {
			success: (data, args) => {},
			error: (error, args) => {
				setPropertyHandler(args[0]?.fieldName, !args[0]?.checked);
			}
		}
	});

	const handleMute = useCallback(
		(chatId, changingData) => {
			updateEntities({
				entity: 'ims-chats',
				entityId: chatId,
				updatingData: {
					members: [
						...chatEntity?.item?.members.filter(
							(member: GroupMember) => member.user.id !== userData.id
						),
						{ ...currentMember, ...changingData }
					]
				}
			});
		},
		[chatEntity?.item]
	);

	const setPropertyHandler = (fieldName: string, checked: boolean) => {
		const chatProperty = { ...propertyRef.current } || {};

		updateEntities({
			entity: 'ims-chats',
			entityId: chatId,
			updatingData: {
				property: {
					...chatProperty,
					[fieldName]: checked
				}
			}
		});
	};

	const handlePropertyChange = useCallback(
		async (event: React.ChangeEvent<HTMLInputElement>) => {
			const name = event.target.name;
			const checked = event.target.checked;

			setPropertyHandler(name, checked);

			//Send api request to change Permission
			await updatePermission({ values: { [name]: checked }, fieldName: name, checked });
		},
		[chatEntity.item.property]
	);

	//mute/unmute notifications
	const { fetchData: handleChatMute } = useAxios({
		url: `/${companyId}/ims/groups/${chatId}/`,
		method: 'put',
		values: {
			is_mute: !isMuted
		},
		cb: {
			success: (data, args) => {},
			error: (error, args) => {
				handleMute?.(chatId, { is_mute: isMuted });
			}
		}
	});

	const { handleLocalDelete } = useEntityForm({
		entity: 'ims-chats',
		name: `AllImsChats-${companyId}-`,
		deleteData: true,
		id: chatId,
		url: '',
		method: 'delete',
		cb: {
			success: (data) => {},
			error: (errors) => {},
			finally: () => {}
		}
	});

	const { handleRequest: handleExitRequest } = useEntityForm({
		entity: 'ims-chats',
		name: `AllImsChats-${companyId}-${groupSearchQuery}`,
		method: open ? 'patch' : 'delete',
		url: open
			? `/${companyId}/ims/groups/${chatId}/members/${memberId}/leave/`
			: `/${companyId}/ims/groups/${chatId}/`,
		deleteData: true,
		id: Number(entityId) as string | number | undefined,
		cb: {
			success: (data) => {
				setExitLoading?.(false); //stop loading spinner
				setExitOpen?.(false); // close modal

				deleteLoading && toggleDeleteLoading();
				deleteVisible && toggleDeleteVisible();

				removeQueryParams(); // close open chat

				if (groupSearchQuery) {
					//Because search mode has different entity name in
					// redux state we must remove chat from not search mode entity too.
					//Or it will remain in chat list when we clear search query from input
					handleLocalDelete();
				}
			},
			error: (error) => {
				setExitLoading?.(false);
				toggleDeleteLoading();
			},
			finally: () => {}
		}
	});

	const deleteGroupChat = () => {
		toggleDeleteLoading();
		handleExitRequest();
	};

	const leaveGroupChat = () => {
		setExitLoading?.(true);
		handleExitRequest();
	};

	const localDelete = () => {
		handleLocalDelete();
		setExitOpen?.(false);
		removeQueryParams();
	};

	const handlerSwitchMute = (isMuting: boolean) => {
		//Optimistic update of state
		handleMute(chatId, { is_mute: !isMuting });

		//CallAsync request
		handleChatMute(isMuting);
	};

	const permissions = [
		{
			Component: BiMessageSquareDetail,
			handleChange: handlePropertyChange,
			iconProp: {},
			text: 'Send Messages',
			field_name: 'is_allowed_send_msg',
			show: isAdmin || isOwner
		},
		{
			Component: CgAdd,
			handleChange: handlePropertyChange,
			iconProp: {},
			text: 'Add Users',
			field_name: 'is_allowed_add_user',
			show: isAdmin || isOwner
		},
		{
			Component: MdOutlineRemoveCircleOutline,
			handleChange: handlePropertyChange,
			iconProp: {},
			text: 'Remove users',
			field_name: 'is_allowed_remove_user',
			show: isAdmin || isOwner
		},
		{
			Component: AiOutlinePushpin,
			handleChange: handlePropertyChange,
			iconProp: {},
			text: 'Pin Messages',
			field_name: 'is_allowed_pin_msg',
			show: isAdmin || isOwner
		},
		{
			Component: AiOutlineEdit,
			handleChange: handlePropertyChange,
			iconProp: { width: 11, height: 11 },
			text: 'Edit Group Info',
			field_name: 'is_allowed_edit_info',
			show: isAdmin || isOwner
		},
		{
			Component: IoExitOutline,
			handleChange: handlePropertyChange,
			iconProp: { className: 'chat-icon-exit' },
			text: 'Exit From Group',
			field_name: 'is_allowed_exit_group',
			show: isAdmin || isOwner
		}
	];

	return {
		leaveGroupChat,
		localDelete,
		permissions,
		handlePropertyChange,
		currentUserProperty:
			chatEntity?.item?.members.find((member: GroupMember) => member?.id === memberId) ?? {},
		property: chatEntity?.item?.property ?? {},
		handlerSwitchMute,
		canLeaveChat,
		canDeleteChat,
		deleteVisible,
		deleteLoading,
		toggleDeleteVisible,
		deleteGroupChat,
		exitLoading,
		open,
		setExitOpen,
		imsData: chatEntity?.item
	};
}
