import React, { useState } from 'react';
import { useDispatch } from 'react-redux';

import { IconAdd, IconEdit, IconExit, IconMessage, IconRemove, IconPin } from 'shared/icons';
import ChatSettingItem from './ChatSettingItem';
import Actions from 'store/actions';
import { useLoadAll } from '../../../../shared/hooks';
const FormRequest = Actions.entity.formRequest;

type Props = {
	companyId: number | undefined;
	chatId: number;
	member: any;
	isOwner: boolean;
	staffMembersLength: number;
	isAdminManager: boolean;
};

const MemberChatSettings = ({
	companyId,
	member,
	chatId,
	isOwner,
	staffMembersLength,
	isAdminManager
}: Props) => {
	const {
		is_allowed_send_msg,
		is_allowed_add_user,
		is_allowed_remove_user,
		is_allowed_pin_msg,
		is_allowed_edit_info,
		is_allowed_delete_group,
		is_allowed_exit_group,
		is_mute
	} = member && member;
	const [property, setProperty] = useState({
		is_allowed_send_msg,
		is_allowed_add_user,
		is_allowed_remove_user,
		is_allowed_pin_msg,
		is_allowed_edit_info,
		is_allowed_delete_group,
		is_allowed_exit_group,
		is_mute
	});

	const { id: memberId } = member;
	const { reloadEntityAll } = useLoadAll();

	const dispatch = useDispatch();

	const handlePropertyChange = (event: any) => {
		const name = event.target.name;
		const checked = event.target.checked;
		const prevProperty = { ...property };
		setProperty((prev) => ({ ...prev, [name]: checked }));
		const payload = { [name]: checked };
		dispatch(
			FormRequest({
				id: member?.id,
				entity: 'chatmembers',
				name: `Allchatmembers-${chatId}-${staffMembersLength}`,
				url: `/${companyId}/ims/groups/${chatId}/members/${memberId}/`,
				method: 'patch',
				params: {},
				updateData: true,
				values: payload,
				normalizeData: (data: any) => data,
				cb: {
					success: (data: any) => {
						reloadEntityAll({
							url: `/${companyId}/ims/${chatId}/members_list/`,
							entity: 'userCompanies',
							name: `Allchatmembers-${chatId}-${staffMembersLength}`,
							params: {
								page: 1,
								limit: 10
							}
						});
					},
					error: (error: Error) => {
						setProperty(prevProperty);
					},
					finally: () => {}
				}
			})
		);
	};

	const permissions = [
		{
			Component: IconMessage,
			handleChange: handlePropertyChange,
			iconProp: {},
			text: 'Send Messages',
			field_name: 'is_allowed_send_msg',
			className: '',
			handler: () => {},
			hasSwitch: true,
			show: isAdminManager || isOwner
		},
		{
			Component: IconAdd,
			handleChange: handlePropertyChange,
			iconProp: {},
			text: 'Add Users',
			field_name: 'is_allowed_add_user',
			className: '',
			handler: () => {},
			hasSwitch: true,
			show: isAdminManager || isOwner
		},
		{
			Component: IconRemove,
			handleChange: handlePropertyChange,
			iconProp: {},
			text: 'Remove users',
			field_name: 'is_allowed_remove_user',
			className: '',
			handler: () => {},
			hasSwitch: true,
			show: isAdminManager || isOwner
		},
		{
			Component: IconPin,
			handleChange: handlePropertyChange,
			iconProp: {},
			text: 'Pin Messages',
			field_name: 'is_allowed_pin_msg',
			className: '',
			handler: () => {},
			hasSwitch: true,
			show: isAdminManager || isOwner
		},
		{
			Component: IconEdit,
			handleChange: handlePropertyChange,
			iconProp: { width: 11, height: 11 },
			text: 'Edit Group Info',
			field_name: 'is_allowed_edit_info',
			className: '',
			handler: () => {},
			hasSwitch: true,
			show: isAdminManager || isOwner
		},
		{
			Component: IconExit,
			handleChange: handlePropertyChange,
			iconProp: { className: 'chat-icon-exit' },
			text: 'Exit From Group',
			field_name: 'is_allowed_exit_group',
			className: 'white-icon',
			handler: () => {},
			hasSwitch: true,
			show: isAdminManager || isOwner
		}
	];

	return (
		<div className="chat-settings__inner">
			<div className="chat-settings__content">
				<div className="chat-settings__title mb-4 font-bold">Individual Permissions</div>
				<div className="chat-settings__l">
					{permissions.map((setting) => {
						const {
							className,
							handler,
							text,
							hasSwitch,
							field_name,
							Component,
							iconProp,
							show,
							handleChange
						} = setting;
						return (
							show && (
								<div className="chat-settings__i" key={field_name}>
									<ChatSettingItem
										className={className}
										handler={handler}
										text={text}
										Component={Component}
										iconProp={iconProp}
										hasSwitch={hasSwitch}
										field_name={field_name}
										handleChange={handleChange}
										property={property}
										isMuteSetting={false}
										isMute={false}
									/>
								</div>
							)
						);
					})}
				</div>
			</div>
		</div>
	);
};

export default MemberChatSettings;
